* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box; }

.container {
  height: 100vh;
  width: 100%; }

.body {
  width: 90%;
  display: flex;
  flex-direction: column; }

.header {
  margin: 20px;
  display: flex;
  justify-content: space-between;
  width: 100%; }
  .header .btn-left button {
    display: inline-block;
    padding: 1rem;
    border: 0.16em solid #ffffff;
    margin: 0 0.3em 0.3em 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #000;
    text-align: center;
    cursor: pointer;
    transition: all 0.15s; }
  .header .btn-left button:hover {
    color: blue;
    border-color: red; }
  .header .btn-left button:active {
    color: #bbbbbb;
    border-color: #bbbbbb; }
  .header .btn-right button {
    display: inline-block;
    padding: 1rem;
    border: 0.16em solid #ffffff;
    margin: 0 0.3em 0.3em 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #000;
    text-align: center;
    cursor: pointer;
    transition: all 0.15s; }
  .header .btn-right button:hover {
    color: blue;
    border-color: red; }
  .header .btn-right button:active {
    color: #bbbbbb;
    border-color: #bbbbbb; }

.card-item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.card {
  background-color: #fef68a;
  border-radius: 20px;
  padding: 1rem;
  min-height: 250px;
  max-width: 250px;
  min-width: 250px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap; }
  .card .note-footer {
    display: flex;
    justify-content: space-between; }
  .card .button {
    margin: 3px;
    width: 1.5rem;
    color: #000; }
  .card .save-cancel {
    display: flex;
    justify-content: space-between; }
  .card textarea {
    width: 200px;
    height: 100px;
    border: 3px solid #cccccc;
    padding: 5px;
    font-family: Tahoma, sans-serif;
    background-position: bottom right;
    background-repeat: no-repeat; }
  .card textarea:focus {
    outline: none; }
  .card .save {
    background-color: #e1e1e1;
    border: none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px; }
  .card .save:hover {
    background-color: #ededed;
    cursor: pointer; }
  .card .date {
    margin-bottom: 10px;
    font: 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif; }
    .card .date input {
      border: 1px solid #c4c4c4;
      border-radius: 5px;
      background-color: #fff;
      padding: 3px 5px;
      box-shadow: inset 0 3px 6px rgba(0, 0, 0, 0.1);
      width: 200px; }
  .card .colors {
    display: flex;
    margin-bottom: 10px; }
    .card .colors div {
      margin: 5px; }
